    import SimpleLightbox from "simplelightbox";
    import "simplelightbox/dist/simple-lightbox.min.css";


    const gallery = document.querySelector(".gallery");

    gallery.insertAdjacentHTML("beforeend", createMarkup(images));

    function createMarkup(array){
        return array
        .map(image=> `<li class="gallery-item">
	<a class="gallery-link" href="${image.original}">
		<img 
			class="gallery-image" 
			src="${image.preview}" 
			alt="${image.description}" 
			/>
	</a>
</li>

        `).join("");
    }
    
    document.addEventListener('DOMContentLoaded', function () {
        var lightbox = new SimpleLightbox('.gallery a', {
          captions: true, // Виводити підписи до зображень
          captionSelector: 'img', // Використовувати елемент <img> для підпису
          captionType: 'attr', // Використовувати атрибут для підпису
          captionsData: 'alt', // Взяти текст для підпису з атрибута alt
          captionPosition: 'bottom', // Розташування підпису
          captionDelay: 250, // Затримка для показу підпису
          showCounter: true, // Показувати лічильник зображень
          enableKeyboard: true, // Дозволити перемикання за допомогою клавіатури
        });
      });